<template>
  	<!-- PAGE CONTENT -->
  	<div class="page__content page__content--with-header page__content--with-bottom-nav" >
      <h2 class="featured__title pb-10">Result</h2>

        <div class="abs-center-wrapper" style="height: calc(100vh - 60px);">
          <div class="abs-center">
            <img src="static/images/ele_result_alert.png" style="width: 200px; margin-bottom: 30px;" />
            <br />
            <span style="font-size: 32px;">alert</span><br />
            <br />
            <span style="font-size: 32px;">The device is not in range</span><br />
            <br />
            <span style="font-size: 32px;">(message)</span><br />
          </div>
        </div>

  	</div>
  <!-- PAGE END -->
</template>

<script>
export default {
  name: "ele_result",
  data() {
    return {
      screenWidth: window.screen.availWidth, // 屏幕宽
      screeHeight: window.screen.availHeight, // 屏幕高
    };
  },
  mounted(){
    window.parent.doCheckScanResultAndGoFlag = false;
    window.parent.QRScanner.getStatus(function(status){
      if (status.scanning) window.parent.QRScanner.cancelScan(function(status){
        document.body.style.backgroundColor = "white";
        window.parent.document.body.style.backgroundColor = "white";
      });
    });
  },
  methods:{
    gopage(){
      // this.$router.push({name:'page2'})
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .abs-center-wrapper {
    margin: 0px;
    padding: 0px;
    height: 100%;
    display: table;
    width: 100%;
  }
  .abs-center-wrapper-no-height {
    margin: 0px;
    padding: 0px;
    display: table;
    width: 100%;
  }
  .abs-center {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
  }
  .abs-center span {
    font-size: 16px;
    font-weight: 500;
    font-family: Roboto, sans-serif;
    color: #0d3567;
  }
</style>



