<template>
  	<!-- PAGE CONTENT -->
  	<div class="page__content page__content--with-header page__content--with-bottom-nav" >
      <h2 class="featured__title pb-10">Result</h2>

        <div class="abs-center-wrapper" style="height: calc(100vh - 60px);">
          <div class="abs-center">
            <img src="static/images/ele_result_success.png" style="width: 200px; margin-bottom: 30px;" />
            <br />
            <span style="font-size: 32px;">Success</span><br />
            <br />
            <span style="font-size: 32px;">{{OpenFloorData}}/F</span><br />
            <span style="font-size: 32px; color: #eb973e;">Waiting at</span><br />
            <br />
            <span style="font-size: 32px;">{{Machine}}</span><br />
          </div>
        </div>

  	</div>
  <!-- PAGE END -->
</template>

<script>
export default {
  name: "ele_result",
  created(){
    var q=this.$route.query;
    if(q){
      var OpenDoorData=q.OpenDoorData;
     var OpenFloorData=q.OpenFloorData;

      if(OpenDoorData!=null&&OpenFloorData!=null){
        console.log(OpenDoorData)
        console.log(OpenFloorData)


         var this_=this;
          //console.log(element)
          this.queryOneQrCodeData(OpenDoorData,function(res){
             if (res) {
            console.log("1111111");
            if (Array.isArray(res)) {
              var buttonData = res[0].Button;
              var Machine=res[0].Machine;
              console.log("1111113");

              console.log("1111114");
              if(buttonData&&Machine){
                console.log(buttonData)
                console.log(Machine)
              var buttonDataObj = buttonData.split(",")

              this_.Machine=Machine;

              console.log(OpenFloorData);
              if(Array.isArray(buttonDataObj)){
                var ofd=buttonDataObj[parseInt(OpenFloorData)];
              console.log(ofd);
              this_.OpenFloorData=ofd
              }


              console.log("1111115");

            }} else {
              console.log("1111112");
            }
          }


          });
    }}
  },
  data() {
    return {
      OpenFloorData:"",
      Machine:"",
      screenWidth: window.screen.availWidth, // 屏幕宽
      screeHeight: window.screen.availHeight, // 屏幕高
    };
  },
  mounted(){
    window.parent.doCheckScanResultAndGoFlag = false;
    window.parent.QRScanner.getStatus(function(status){
      if (status.scanning) window.parent.QRScanner.cancelScan(function(status){
        document.body.style.backgroundColor = "white";
        window.parent.document.body.style.backgroundColor = "white";
      });
    });
  },
  methods:{
    gopage(){
      // this.$router.push({name:'page2'})
    },queryOneQrCodeData(qrId,cbfn){
      var p=window.parent;
    if(p){
      var app=p.app;
      if(app){
        var DbUtil=app.DbUtil;
        if(DbUtil){
          DbUtil.queryOneQrCodeData(p,qrId,function(res){

              if(cbfn){
                cbfn(res)
              }
          })
        }
      }
    }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .abs-center-wrapper {
    margin: 0px;
    padding: 0px;
    height: 100%;
    display: table;
    width: 100%;
  }
  .abs-center-wrapper-no-height {
    margin: 0px;
    padding: 0px;
    display: table;
    width: 100%;
  }
  .abs-center {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
  }
  .abs-center span {
    font-size: 16px;
    font-weight: 500;
    font-family: Roboto, sans-serif;
    color: #0d3567;
  }
</style>



