<template>
  <!-- PAGE CONTENT -->
  <div class="page__content page__content--with-header page__content--with-bottom-nav" style="height: 100vh;">
    <div id="ele_floor-selection">
      <h2 class="featured__title pb-10">Which floor are you going to?</h2>
      <div style="height: 30px;"></div>
      <ul class="ele_floor" v-for="(item, index) in ButtonDatas" v-bind:key="index">
        <li v-on:click="openDoor(index)">{{ item.Text }}</li>
      </ul>
    </div>
    <div id="ele_floor-waiting">
      <h2 class="featured__title pb-10">The lift is approaching</h2>
      <div style="height: 30px;"></div>
      <div class="landscape-padding">
        <div class="abs-center-wrapper landscape" style="width: 50%; height: calc(100vh - 57px - 60px - 80px); float: left;">
          <div class="abs-center">
              <img src="static/images/upndown.gif" style="width: 120%; transform: translate(-5%, 0); margin-top: 100px; filter: grayscale(100%);" />
          </div>
        </div>
        <div class="abs-center-wrapper landscape rotate landscapeAndRotate" style="width: 50%; height: calc(100vh - 57px - 60px - 80px); float: right;">
          <div class="abs-center">
            <img src="static/images/upndown.gif" style="width: 120%; margin-top: 100px; filter: grayscale(100%); transform: translate(-5%, 0);" />
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- PAGE END -->
</template>

<script>
export default {
  name: "ele_floor",
  data() {
    return {
      ButtonDatas: [],
      OpenDooData: "",
      DeviceID: "",
      screenWidth: window.screen.availWidth, // 屏幕宽
      screeHeight: window.screen.availHeight, // 屏幕高
      Type:""
    };
  },
  created() {
    console.log("ele_floor_result");
    var this_ = this;

    var q = this.$route.query;
    if (q) {
      var OpenDoorData = q.OpenDoorData;
      this_.OpenDoorData = OpenDoorData;

      if (OpenDoorData != null) {
        console.log(OpenDoorData);

        //console.log(element)
        this_.queryOneQrCodeData(OpenDoorData, function(res) {
          if (res) {
            if (Array.isArray(res)) {
              var buttonData = res[0].Button;
              var outputs = res[0].OutPut;
              if (buttonData && outputs) {
                var buttonDataObj = buttonData.split(",");
                var outputsObj = outputs.split(",");
                this_.DeviceID = res[0].DevId;
                this_.Type=res[0].Type;
                if (buttonDataObj && Array.isArray(buttonDataObj)) {
                  for (let index = 0; index < buttonDataObj.length; index++) {
                    var element = buttonDataObj[index];
                    var o = outputsObj[index];
                    if (/^\+?(0|[1-9]\d*)$/.test(element+"")) {
                      element = element + "/F";
                    }
                    var d = { Text: element, OutPut: o };
                    // alert(o);
                    this_.ButtonDatas.push(d);
                  }
                  console.log("1111116");
                }
              }
            } else {
              console.log("1111112");
            }
          }
        });
      }
    }
  },
  mounted() {
    window.parent.doCheckScanResultAndGoFlag = false;
    window.parent.QRScanner.getStatus(function(status){
      if (status.scanning) window.parent.QRScanner.cancelScan(function(status){
        document.body.style.backgroundColor = "white";
        window.parent.document.body.style.backgroundColor = "white";
      });
    });

    if(this_.Type){

    var this_ = this;
    var p = window.parent;
    if (p) {
      var buttonData=p.app.ButtonConfigData;
      if(buttonData){
        var show=buttonData[this_.Type].saveButton;
        if(show!=null&&show==true){
      var navigator = p.navigator;
      if (navigator && navigator.notification) {
        var onConfirm = function(buttonIndex) {
          console.log("You selected button " + buttonIndex);
          if (buttonIndex != null) {
            if (buttonIndex == 1) {
              this_.updateAllSerachData(this_.OpenDooData);
            }
          }
        };

        navigator.notification.confirm(
          "Do you want to save the data?", // message
          onConfirm, // callback to invoke with index of button pressed
          "Dialog", // title
          ["Yes", "No"] // buttonLabels
        );
      }
    }}}}
  },
  methods: {
    doTest(cbfn) {
      var ButtonDatas = [];
      var d = { Text: "1F", OutPut: 105 };
      var d1 = { Text: "2F", OutPut: 106 };
      ButtonDatas.push(d1);
      ButtonDatas.push(d);
      if (cbfn) {
        cbfn(ButtonDatas);
      }
    },
    queryOneQrCodeData(qrId, cbfn) {
      var p = window.parent;
      if (p) {
        var app = p.app;
        if (app) {
          var DbUtil = app.DbUtil;
          if (DbUtil) {
            DbUtil.queryOneQrCodeData(p, qrId, function(res) {
              if (cbfn) {
                cbfn(res);
              }
            });
          }
        }
      }
    },
    updateAllSerachData(qrCodeId) {
      var this_ = this;
      if (window) {
        var parent = window.parent;
        if (parent) {

            var app = parent.app;
            if (app) {
              var DbUtil = app.DbUtil;
              var DoorMasterSdkUtil = app.DoorMasterSdkUtil;
              if (DbUtil) {
                DbUtil.updateAllowSearch(parent, qrCodeId, function(qrCodeIds) {
                  app.showDataFn(qrCodeIds);
                });
              }
            }

        }
      }
    },
    openDoor(floorIndex) {
      var output = this.ButtonDatas[floorIndex].OutPut;
      console.log(output);
      var DeviceID = this.DeviceID;
      console.log(DeviceID);
      try {
        var parent = window.parent;
        if (parent) {
          var app = parent.app;
          parent.OpenFloorData = floorIndex;
          if (app != null) {
            var DoorMasterSdkUtil = app.DoorMasterSdkUtil;
            if (DoorMasterSdkUtil != null) {


              DoorMasterSdkUtil.openDoor(parent, DeviceID, output);
            }
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
  }
};
window.switchPane = function(pane) {
  if (pane == 'ele_floor-selection') {
    document.getElementById('ele_floor-selection').style.display = "block";
    document.getElementById('ele_floor-waiting').style.display = "none";
  } else if (pane == 'ele_floor-waiting') {
    document.getElementById('ele_floor-selection').style.display = "none";
    document.getElementById('ele_floor-waiting').style.display = "block";
  } else {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .ele_floor {
    width: 100%;
    margin-bottom: 40px;
  }
  .ele_floor li {
    text-align: center;
    padding: 16px;
    font-size: 20px;
    font-weight: 500;
    font-family: Roboto, sans-serif;
    color: #0d3567;
    border-bottom: solid 1px #0d3567;
  }
  #ele_floor-selection {
    display: block;
  }
  #ele_floor-waiting {
    display: none;
  }
  .rotate {
    -webkit-transform:rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  @media only screen and (orientation: landscape) {
    .landscape-padding {
      margin-top: 10vh;
    }
    .landscape {
      margin-top: -50vh;
      transform: scale(0.6, 0.6);
    }
    .landscapeAndRotate {
      margin-top: -50vh;
      -webkit-transform:rotate(180deg) scale(0.6, 0.6);
      -moz-transform: rotate(180deg) scale(0.6, 0.6);
      -ms-transform: rotate(180deg) scale(0.6, 0.6);
      -o-transform: rotate(180deg) scale(0.6, 0.6);
      transform: rotate(180deg) scale(0.6, 0.6);
    }
  }
</style>



