<template>
  <!-- PAGE CONTENT -->
  <div class="page__content page__content--with-header page__content--with-bottom-nav" style="height: 100vh; background-color: transparent;">
    <div class="waiting">
      <h2 class="featured__title pb-10" style="color: white;">The camera is scaning</h2>
      <div style="height: 30px;"></div>
      <div class="landscape-padding">
        <div style="float: right; height: 80vh;">
            <img v-on:click="torchToggle()" style="border-radius: 50%; padding: 10px; backgroundColor: #d3d3d3; width: 44px; margin-top: 74vh; margin-right: 0px;" src="data:image/png;base64," />
        </div>
      </div>
    </div>
  </div>
  <!-- PAGE END -->
</template>

<script>
export default {
  name: "scan",
  data() {
    return {
      torch: false,
      screenWidth: window.screen.availWidth, // 屏幕宽
      screeHeight: window.screen.availHeight, // 屏幕高

    }
  },
  created(){

  },
  mounted(){
    document.body.style.backgroundColor = "transparent";
    window.parent.document.body.style.backgroundColor = "transparent";

    if(window){
      if(window.parent){
        var isSim = parent.device.isVirtual;
        if (!isSim) {
          window.parent.app.DoorMasterSdkUtil.checkDevices(window.parent,"0");
        } else {
          setTimeout(function(){
            window.parent.cordovaScanCallBack("{ \"IsCheck\" : true, \"ScanResult\" : { \"DeviceSnResultList\" : \"3649103924,3648178351,0538998836,3644995689,1421917393\", \"RssiResultList\" : \"-58,-53,-74,-57,-66\", \"Scan_Device_Scan_Result\" : false }, \"Go_Next_Flag\" : 40 }");
          }, 3000);
        }
      }
    }
  },
  methods: {
    torchToggle() {
      if (this.torch) {
        this.torch = false;
        window.parent.QRScanner.disableLight(function(err, status){    });
      } else {
        this.torch = true;
        window.parent.QRScanner.enableLight(function(err, status){    });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



