import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home';
import scan from '@/components/scan';
import ele_call from '@/components/ele_call';
import ele_floor_result from '@/components/ele_floor_result';
import ele_floor_scan from '@/components/ele_floor_scan';
import ele_result from '@/components/ele_result';
import ele_alert from '@/components/ele_alert';
import terms from '@/components/terms';
import privacy from '@/components/privacy';
import about from '@/components/about';
// import page2 from '@/views/page2';
// import page3 from '@/views/page3';
// import page4 from '@/views/page4';
// import page5 from '@/views/page5';
// import page6 from '@/views/page6';

Vue.use(Router)

export default new Router({
  routes: [
    { name: 'home', path: '/', component: home },
    { name: 'scan', path: '/scan', component: scan },
    { name: 'ele_call', path: '/ele_call', component: ele_call },
    { name: 'ele_floor_scan', path: '/ele_floor_scan', component: ele_floor_scan },
    { name: 'ele_floor_result', path: '/ele_floor_result', component: ele_floor_result },
    { name: 'ele_result', path: '/ele_result', component: ele_result },
    { name: 'ele_alert', path: '/ele_alert', component: ele_alert },
    { name: 'terms', path: '/terms', component: terms },
    { name: 'privacy', path: '/privacy', component: privacy },
    { name: 'about', path: '/about', component: about },
    // { name: 'page2', path: '/', component: page2 },
    // { name: 'page3', path: '/page3', component: page3 },
    // { name: 'page4', path: '/page4', component: page4 },
    // { name: 'page5', path: '/page5', component: page5 },
    // { name: 'page6', path: '/page6', component: page6 },
  ]
})



