<template>
  <!-- PAGE CONTENT -->
  <div
    class="page__content page__content--with-header page__content--with-bottom-nav"
    style="height: 100vh;"
  >
    <h2 class="featured__title pb-10">{{ message }}</h2>
    <div style="height: 30px;"></div>


      <h3>Go Up</h3>
      <div class="page__title-bar" style="height: calc(45vh - 100px);">
        <div
          class="page__title-right"
          style="clear: both; display: block; margin-left: auto; margin-right: auto;"
        >
          <img src="static/images/btn_goup.png" v-on:click="goUP()"/>
        </div>
      </div>

    <div
      class="swiper-container slider-thumbs slider-init mb-20"
      data-paginationtype="progressbar"
      data-spacebetweenitems="10"
      data-itemsperview="auto"
    >
      <div class="swiper-pagination slider-thumbs__pagination"></div>
    </div>

    <h3>Go Down</h3>
    <div class="page__title-bar" style="height: calc(45vh - 100px);">
      <div
        class="page__title-right"
        style="clear: both; display: block; margin-left: auto; margin-right: auto;"
      >
        <img src="static/images/btn_godown.png" v-on:click="goDown()"/>
      </div>
    </div>
  </div>
  <!-- PAGE END -->
</template>

<script>
export default {
  name: "ele_call",
  created() {
    //this.doTest();
    var this_=this;
    var q=this.$route.query;
    if(q){
      var QrIds=q.QrIds;
      if(QrIds!=null){
        var QrIds_=JSON.parse(QrIds);
        this_.QrIds=QrIds_;
        for (let index = 0; index < QrIds_.length; index++) {
          const element = QrIds_[index];
          this_.queryOneQrCodeData(element, function(res) {
          		var CallButtonDevId=res[0].CallButtonDevId;
          		if(this_.BtnList.length>0){
          			if( this_.BtnList.indexOf(CallButtonDevId)==-1){
          				this_.BtnList.push(CallButtonDevId);
          			}
          		}else{
          			this_.BtnList.push(CallButtonDevId);
          		}

          });
        }
      }
    }

  },
  mounted(){
    window.parent.doCheckScanResultAndGoFlag = false;
    window.parent.QRScanner.getStatus(function(status){
      if (status.scanning) window.parent.QRScanner.cancelScan(function(status){
        document.body.style.backgroundColor = "white";
        window.parent.document.body.style.backgroundColor = "white";
      });
    });
  },
  data() {
    return {
      visible: false,
      BtnList: [],
      QrIds:[],
      message: "Do you want to go up?",
      screenWidth: window.screen.availWidth, // 屏幕宽
      screeHeight: window.screen.availHeight // 屏幕高
    };
  },
  methods: {
    doTest(){
      this.BtnList.push(101)
      this.BtnList.push(102)
    },
    queryOneQrCodeData(qrId,cbfn){
      var p=window.parent;
    if(p){
      var app = p.app;
      if (app) {
        var DbUtil=app.DbUtil;
        if(DbUtil){
          DbUtil.queryOneQrCodeData(p,qrId,function(res){
              if(cbfn){
                cbfn(res)
              }
          })
        }
      }
    }
    },
    goUP() {
      //0'
      var this_=this;
      setTimeout(function() {
        this_.openDoor(0);
      }, 500);

    },
    goDown() {
      //1
      var this_=this;
      setTimeout(function() {
        this_.openDoor(1);
      }, 500);
    },
    openDoor(index) {
      const DeviceID = this.BtnList[index];
      try {
        var parent = window.parent;
        if (parent) {
          var app = parent.app;
          if (app != null) {
            var DoorMasterSdkUtil = app.DoorMasterSdkUtil;
            if (DoorMasterSdkUtil != null) {
              parent.OpenDoorData = this.QrIds;
              var isSim = parent.device.isVirtual;
              if (!isSim) {
                DoorMasterSdkUtil.openDoor(parent, DeviceID, -1, function(returnData) {
                  app.showDataFn(returnData);
                });
              } else {
                window.parent.cordovaCallBack("0");
              }
            }
          } else {
            console.log("app anull");
          }
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



